<template>
	<div id="app">
		<audio autoplay controls src="../static/钢琴-梦中的婚礼.mp3" id="audiomp3" loop style="display: none;"></audio>
		<div id="audioBox"></div>
		<keep-alive>
			<router-view></router-view>
		</keep-alive>
	</div>
</template>

<script>
	export default {
		name: 'app',
		components: {
			// HelloWorld
		},
		data() {
			return {
				screenWidth: document.body.clientWidth,
				timer: false,
			}
		},
		watch: {
			screenWidth(val) {
				// 为了避免频繁触发resize函数导致页面卡顿，使用定时器
				if (!this.timer) {
					// 一旦监听到的screenWidth值改变，就将其重新赋给data里的screenWidth
					this.screenWidth = val
					this.timer = true
					let that = this;
					setTimeout(function() {
						// 打印screenWidth变化的值
						if (that.screenWidth < 1000) {
							that.$router.push('mobileIndex')
						} else {
							that.$router.push('pcIndex')
						}
						that.timer = false
					}, 200)
				}
			}
		},
		created() {
			let that = this;
			// 再经过一个路由
			// that.$router.push('homepage')
			// 直接进入手机端或pc
			if (that.screenWidth < 1000) {
				that.$router.push('mobileIndex')
			} else {
				that.$router.push('pcIndex')
			}

		},
		mounted() {
			// 播放多个音乐

			const that = this
			window.onresize = () => {
				return (() => {
					window.screenWidth = document.body.clientWidth
					that.screenWidth = window.screenWidth
				})()
			}
		},
		methods: {

		}
	}
</script>

<style>
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		color: #2c3e50;
		height: 100%;
		width: 100%;
	}
</style>
